*{margin:0;padding:0;box-sizing:border-box}a{color:inherit;text-decoration:inherit}a:hover{opacity:0.5}.container{width:90%;max-width:1150px;margin:0 auto}.flex{display:flex}.question-box__title{margin-bottom:15px;padding:25px;font-weight:normal;background:#7f2838;color:black;border-radius:15px;box-shadow:5px 5px 13px 0px rgba(0,0,0,0.5);border:2px outset #7f2838}.question-box__btn{width:100%;padding:10px;cursor:pointer;font-weight:bold;border-radius:10px;box-shadow:5px 5px 13px 0px rgba(0,0,0,0.5);outline:0}.question-box__btn.answer-btn{margin-bottom:15px;background:#BCD952;color:black}.question-box__btn.answer-btn.answer-btn:hover{background:#24bfa38a}.question-box__btn.confirm-btn{margin-top:30px;background:#EBF21D;color:black;-webkit-animation:intermittence 2s infinite 100ms;animation:intermittence 2s infinite 100ms}.question-box__btn.cancel-btn{margin-top:10px;background:#f2961d;color:black}@-webkit-keyframes intermittence{0%{background:#EBF21D}50%{background:#ebf21d67}100%{background:#EBF21D}}@keyframes intermittence{0%{background:#EBF21D}50%{background:#ebf21d67}100%{background:#EBF21D}}@media (min-width: 768px){.question-box{margin:0 auto;max-width:350px}}

